<!-- 24小时告警趋势 -->
<template>
<div class="content">
	<div class="title">
		<span> {{ static.title }} </span>
	</div>
	<div class="container" style=""></div>
</div>
</template>

<script>
function initChart(ele, list, outOpt){
	var dataList = formatList(list);

	var myChart ;
	var dom = ele.querySelector('.container');
	if ( $(dom).find('canvas').length > 0 ) {
	  myChart = echarts.getInstanceByDom( dom )
	}else{
	  myChart = echarts.init( dom );
	}
	
	var option = null;
	option = {
		//backgroundColor:'#054466',
		grid:{
		  bottom:'8%',
		  top:'24%',
		  left:'0%', 
		  right:'0%',
		},
		tooltip: {
			trigger: 'axis',
			formatter: '{b} : {c}'
		},
		legend: {
			right:'10%',
			top:'15%',
			itemWidth: 20,
			itemHeight: 10,
			itemGap: 10,
			textStyle: {
				fontSize: 14,
				color: '#F79666'
			}
		},
		xAxis: {
			type: 'category',
			data: dataList.axis,
			axisTick:{
				show:false
			},
			axisLabel:{
				textStyle: {            
					fontSize:14,
					color:'#cccccc',
				}
			},
			axisLine:{
					lineStyle:{
						color:'#284553',
						
					}
			}
		},
		yAxis: {
			type: 'value',
			boundaryGap: [0, 0.1], 
			axisTick:{
				show:false
			},
			splitLine:{  
					　lineStyle: {
						 color: '#0D2E3C',
						 type:'dotted'
					  }
			},
			axisLine:{
					lineStyle:{
						color:'#284553',
						
					}
			}       
		},
		series: [{
			name:'告警',
			data: dataList.data1,
			type: 'line',
			smooth: true,
			symbol: 'circle',
			itemStyle: {
				normal: {
					color: "#F79666",
					lineStyle: {
						color: "#F79666"
					}
				}
			},              
		}]
	};

	if (option && typeof option === "object") {
		myChart.setOption(option, true);
		myChart.setOption(outOpt, false);
	}
}

function formatList( list ){
	var axis = [];
	var data1 = [];
	var data2 = [];
	list.forEach( function(item, index) {
		axis.push( item.interval );
		data1.push( item.num_alert );
	});
	return {
		axis,
		data1
	}
}

export default {
	name: '',
	props: {
		list: {
			default(){
				return aged1.day_tendency_alert.day_tendency_alert_door
			}
		},
		outOpt: {
			default(){
				return {}
			}
		},
		static: {
			default(){
				return {
					"title": "24小时告警趋势:"
				}
			}
		}
	},
	data () {
		return {
		  
		}
	},
	mounted(){
	  this.update();
	},
	watch: {
		list: function (val, oldV) {
			this.update();
		}
	},
	methods: {
		update (){
			initChart(this.$el , this.list, this.outOpt);
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.title{
	margin-left:20px;
	margin-top:17px;
	font-size:30px;
	color:#00ffee;
}
.container{
	margin-top:67px;
	height: 350px;
	width:474px;
	margin-left:52px;
	background:url(../../static/img/gaojing.png) no-repeat center 5px !important;
}
.content{
	margin:0;
	border-radius: 8px;
	height: 512px;
	width:570px;
	/*background-color:#001122*/
}
</style>


