<!-- 今日告警 -->
<template>
  <div class="content">
    <div class="title">
      <img class="imgtit" src="../../static/img/warningtoday.png"/>
      <span>{{static.title}}</span>
    </div>
    <div class="container" style="">

      <span class="timesspan">{{static.num}}</span>
      <span class="text-style-1">次</span>
      <img  src="../../static/img/warningtoday2.png"/>

    </div>
  </div>
</template>

<script>
export default {
	name: '',
	props: {

		static: {
			default(){
				return {
					"title": '今日告警',
				}
			}
		}
	},
	data () {
		return {

		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .content{
    margin:0;
    height: 265px;
    width:400px;
  }

  .title{

    font-size:30px;

    color:#00ffee;

    margin-top:10px;
    margin-bottom:60px;
  }
  .imgtit{
    float:left;
    margin-left:20px;
    margin-right:7px;

  }
  .container{
    height:120px;

  }

  .timesspan {
    display: inline-block;
    width: 140px;
    text-align: right;
    height: 50px;
    font-family: MicrosoftYaHei-Bold;
    font-size: 50px;
    font-weight: normal;
    font-stretch: normal;
    line-height: 40px;
    letter-spacing: 0px;
    color: #d81e06;

    margin-left:0;
    margin-top:20px;
  }

  .text-style-1 {
    font-family: MicrosoftYaHei;
    font-size: 30px;
    font-weight: normal;
    letter-spacing: 0px;
    color: #d81e06;
    margin-right:53px;
  }
  .timesspan,.text-style-1,.container>img{

    vertical-align: middle;

  }
</style>



