

// xList: ['老丰阁1','老丰阁2','老丰阁3','老丰阁4','老丰阁','老丰阁',"老丰阁","老丰阁"],
// "dataList": [100, 90,80,70,60,50,40,30]

export function initBarh2 (dom, list, outOpt){
	// var myData = formatList(list);
	var myChart ;
	if ( $(dom).find('canvas').length > 0 ) {
	  myChart = echarts.getInstanceByDom( dom )
	}else{
	  myChart = echarts.init( dom );
	}
	var colorList = ["#6a191c", "86262a", "#b6353a", "#d46559", "#e29856", "#eda639", "#f6cb4a", "#fceca6"];
	var option = null;

	option = {
	  tooltip: {
	    trigger: 'axis',
	    axisPointer: {
	      type: 'shadow'
	    }
	  },
	  grid: {
	    left: '10%',
	    top:"10%",
	    right: '10%',
	    bottom: '10%',
	    containLabel: true
	  },
	  xAxis: {
	    type: 'value',
	    show:false
	  },
	  yAxis: {
	    type: 'category',
	    axisLine:{
	      show:false
	    },
	    axisTick:{
	      show:false
	    },
	    axisLabel:{
	      show:true,
	      margin:40,
	      color:"#fbfbfb",
	      fontSize:20
	    },
	    data: list.xList.reverse()
	  },
	  series: [
	    {
	      type: 'bar',
	      barWidth:30,
	      data: list.dataList.reverse().map(function(item, index) {
	        // console.log( item, index)
	        return {
	          value: item,
	          itemStyle:{
	          	normal: {
	          		color:  colorList[index%9],
	          		barBorderRadius:15
	          	},
	          	emphasis: {
	          		color:  colorList[index%9],
	          		barBorderRadius:15
	          	}
	          }
	        }
	      })       
	    }
	  ]
	};

	if (option && typeof option === "object") {
		myChart.setOption(option, true);
		myChart.setOption(outOpt, false);
	}
}




