<!-- 食品安全 -->
<template>
	<page-box class="community">
		<!-- 左边 -->
		<div class="c_left clear" slot="left">
			<div class="left_1 clear">
				<todayWarning :static="static.left0101"></todayWarning>
				<deploy
					:static="static.left0102"
				></deploy>
			</div>
			<div class="left_2">
				<div class="left_2_1">
					<warnSum2
						:static="static.left02"
					></warnSum2>
				</div>
				
			</div>
			<div class="left_3">
				<div class="com_title">{{ static.left03.title }}</div>
				<div class="cont flex">
					<div class="flex_item" v-for="item in static.left03.dataList">
						<p class="pic_box"><img :src="`static/img/${item.img}`"><span class="num">{{item.num}}</span></p>
						<p class="desc">{{item.name}}</p>
					</div>
				</div>
			</div>
			<div class="left_4">
				<div class="com_title">{{static.left04.title}}</div>
				<div class="chart"></div>
			</div>
		</div>
		<!-- 中间 -->
		<div class="c_center clear" slot="center">
			<div class="center_1">
				<iframe :src="static.centerTop01.iframe" class="cont"></iframe>
			</div>
			<div class="center_2">
				<div class="com_title">{{ static.center02.title }}</div>
				<div class="cont"><img class="pic" :src="`static/img/${static.center02.img}`"></div>
			</div>
			<div class="center_3 flex">
				<div class="flex_item" 
					v-for="item in static.center03.dataList"
				><img :src="`static/img/${item.img}`"></div>
			</div>
		</div>
		<!-- 右边 -->
		<div class="c_right clear" slot="right">
			<div class="right_1">
				<div class="com_title">{{static.right01.title}}</div>
				<table class="table">
					<tbody>
						<tr>
							<th v-for="item in static.right01.thList">{{item}}</th>
						</tr>
						<tr v-for="item in static.right01.tdList">
							<td>{{item.col1}}</td>
							<td>{{item.col2}}</td>
							<td>{{item.col3}}</td>
							<td><span class="txt">{{item.col4}}<span class="icon" :class="item.col4Icon"></span></span></td>
						</tr>
					</tbody>
				</table>
			</div>
			<div class="right_2">
				<div class="com_title">{{static.right02.title}}</div>
				<div class="chart"></div>
			</div>
			<div class="right_3">
				<div class="com_title">{{static.right03.title}}</div>
				<div class="chart"></div>
			</div>
			<div class="right_4 street_right_4">
				<warnOccurArea
					:list="static.right04.dataList"
					:static="static.right04"
					:outOpt="right04Opt"
				></warnOccurArea>
			</div>
		</div>
	</page-box>
</template>

<script>

// 组件
import PageBox from '@/modules/page-box.vue';

import todayWarning from '@/components/todayWarning.vue';
import deploy from '@/components/deploy.vue';
import warnSum2 from '@/components/warnSum2.vue';

import warnOccurArea from '@/components/warnOccurArea.vue';

// echart
import {initBarh2} from '@/charts/barh2.js';
import {initLineBar01} from '@/charts/lineBar01.js';
import {initLineTwo01} from '@/charts/lineTwo01.js';

// js
import {formatChartList} from '@/assets/js/assets.com.js';
// js
import {insertScript} from '@/assets/js/assets.com.js';

// 数据
import pageJson from '@/../static/pageJson/street.json';

export default {
	name: '',
	components: {
		PageBox,
		todayWarning, deploy, warnSum2,
		warnOccurArea
	},
	data () {
		return {
		  static: pageJson,
		  timer: '', // 计时器
		  right04Opt: {
		  	grid:{
		  	  bottom:'8%',
		  	  top:'14%',
		  	  left:'5%', 
		  	  right:'0%',
		  	},
		  	legend: {
		  		show: false
		  	},
		  	yAxis: {
  				type: 'value',
  				boundaryGap: [0, 0.1], 
  				axisLabel:{
  					textStyle: {            
  						fontSize:14,
  						color:'#cccccc',
  					}
  				},
  				splitLine:{  
					　lineStyle: {
						 color: '#0D2E3C',
						 type:'dotted'
					  }
  				},
  				axisLine:{
						lineStyle:{
							color:'#0D2E3C',
						}
  				}       
  			},
  			series:[
  				{
  					name: "公园"
  				}
  			]
		  }
		}
	},
	mounted(){
		var timerI = 0;
		var _vue = this;

		_vue.queryData(timerI);

		this.timer = setInterval(function () {
			console.log( 'community', timerI )
		  _vue.queryData(timerI);

		  timerI++;
		  timerI%=5;
		}, 30000);
	},
	beforeRouteLeave (to, from, next){
		clearInterval( this.timer );
		next();
	},
	methods: {
		updata(){
			// 更新数据的操作放着里
			// 组件更新为监听数据变化

			// 告警频繁TOP5街道
			var list1 = formatChartList(this.static.right03.dataList );
			initBarh2(this.$el.querySelector('.right_3 .chart'), {
				xList: list1.xList,
				dataList: list1.data1
				},{
				grid: {
				  left: '10%',
				  top:"0%",
				  right: '10%',
				  bottom: '0%',
				  containLabel: true
				}
			});
			// 重点区域人流量告警
			var right04Data = this.static.left04;
			initLineBar01(this.$el.querySelector('.left_4 .chart'), right04Data);

			// 商场人流历史趋势图
			var right02Data = this.static.right02;
			initLineTwo01(this.$el.querySelector('.right_2 .chart'), right02Data);
		},
		queryData(index){
			var _vue = this;
			
			insertScript( `./static/data/street/data${index}.js` );
			// $('#myScript').html( `'<script data-type=static src=""><\/script>'` );
			_vue.static = $.extend(true, {}, pageJson, staticData );;
			_vue.updata();
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
	@import url(../../../assets/less/var.less);
	.community{
		div{
			box-sizing: border-box;
		}
		.com_title{
			font-size: 30px;
			line-height: 60px;
			letter-spacing: 0px;
			color: #00fff0;
			padding-left: 40px;
		}
		// 坐边
		.c_left{
			padding-left: 40px;
			padding-bottom: 15px;
			>div{
				float: left;
			}
			.left_all(){
				overflow: hidden;
				box-sizing: border-box;
			}
			.left_1{
				.left_all();
				width: 100%;
				height: 265px;
				margin-bottom: 15px;
				border-radius: 8px;
				border: solid 1px rgba(56, 174, 225, 0.71);
			}
			.left_2{
				.left_all();
				width: 400px;
				height: 330px;
				border: solid 1px rgba(56, 174, 225, 0.71);
				border-radius: 8px;
				.left_2_1{
					height: 330px;
				}
			}
			.left_3{
				.left_all();
				width: 785px;
				margin-left: 15px;
				height: 330px;
				border: solid 1px rgba(56, 174, 225, 0.71);
				border-radius: 8px;
			}
			.left_4{
				width: 100%;
				height: 330px;
				margin-top: 15px;
				border: solid 1px rgba(56, 174, 225, 0.71);
				border-radius: 8px;
			}
		}
		// 中间
		.c_center{
			padding: 0 20px 0 20px;
			>div{
				float: left;
			}
			.center_1{
				width: 1390px;
				height: 600px;
				margin-bottom: 20px;
				border-radius: 8px;
				// border: solid 1px rgba(56, 174, 225, 0.71);
			}
			.center_2{
				width: 450px;
				height: 330px;
				border-radius: 8px;
				border:1px solid rgba(56, 174, 225, 0.71);
				margin-right: 10px;
			}
			.center_3{
				width: 920px;
				margin-right: 10px;
				height: 330px;
				border-radius: 8px;
				border:1px solid rgba(56, 174, 225, 0.71);
			}
		}
		// 右边
		.c_right{
			height: 100%;
			width: 100%;
			padding-right: 30px;
			padding-bottom: 15px;
			>div{
				float: left;
			}
			.right_1{
				width: 570px;
				height: 510px;
				margin-bottom: 15px;
				border-radius: 8px;
				border: solid 1px rgba(56, 174, 225, 0.25);
			}
			.right_2{
				width: 555px;
				height: 510px;
				margin-bottom: 15px;
				margin-left: 15px;
				border-radius: 8px;
				border: solid 1px rgba(56, 174, 225, 0.25);
			}
			.right_3{
				width: 570px;
				height: 430px;
				border-radius: 8px;
				border: solid 1px rgba(56, 174, 225, 0.25);
			}
			.right_4{
				width: 555px;
				height: 430px;
				margin-bottom: 15px;
				margin-left: 15px;
				border-radius: 8px;
				border: solid 1px rgba(56, 174, 225, 0.25);
			}
		}

		// 左边内容
		.left_1{
			>div{
				float: left;
			}
		}
		.left_3{
			.cont{
				padding-top: 60px;
			}
			.flex{
				justify-content: space-around;
				text-align: center;
			}
			.flex_item{
				flex: 1 1 auto;
				padding-right: 20px;
			}
			.pic_box{
				white-space: nowrap;
				position: relative;
				img{
					height: 60px;
					vertical-align: middle;
				}
			}
			.num{
				position: absolute;
				right: 0px;
				top: 0;
				vertical-align: top;
				display: inline-block;
				margin-left: 5px;
				width: 30px;
				height: 30px;
				line-height: 30px;
				background-color: #ff0000;
				font-size: 16px;
				text-align: center;
				color: #eaeaea;
				border-radius: 50%;
			}
			.desc{
				padding-top: 20px;
			}
		}
		.left_4{
			.chart{
				width: 100%;
				height: 260px;
			}
		}

		.center_2{
			overflow: hidden;
			.cont{
				width: 100%;
				height: 260px;
				padding: 10px;
			}
			.pic{
				width: 100%;
				height: 100%;
			}
		}
		.center_3{
			&.flex{
				flex-wrap: wrap;
				padding: 10px;
			}
			.flex_item{
				flex: 0 0 25%;
				height: 50%;
				padding: 10px;
				img{
					height: 100%;
					width: 100%;
				}
			}
		}

		// 中间内容
		.center_1{
			position: relative;
			img{
				position: absolute;
				left: 0;
				right: 0;
				height: 100%;
				width: 100%;
			}
		}

		
		.right_1{
			.table{
				.mytable01();
				th{
					font-size: 14px;
					color: #b4b4b4;
					padding: 20px 0;
				}
				td{
					font-size: 18px;
					padding: 20px 0;
					color: #e0e0e0;
				}
				.txt{
					position: relative;
				}
				.icon{
					display: inline-block;
					position: absolute;
					right: -20px;
					top: 50%;
					margin-top: -15px;
					width: 6px;
					height: 30px;
					background-repeat: no-repeat;
					background-position: center;
					background-size: cover;
					&.up{
						background-image: url(../../../../static/img/up.png);
					}
					&.down{
						background-image: url(../../../../static/img/down.png);
					}
				}
			}
			.chart{
				margin-top: 20px;
				height: 420px;
				width: 90%;
				margin-left: 5%;
			}
		}
		.right_2{
			.chart{
				height: 440px;
			}
		}
		.right_3{
			.chart{
				height: 370px;
			}
		}
		.right_4{
			.chart{
				height: 370px;
			}
			.content{
				width: 100%;
				height: 100%;
			}
			.container{
				width: 100%;
				height: 370px;
				padding: 0;
			}
		}
	}
</style>

<style lang="less">
	.street_right_4{
		.chart{
			height: 370px;
		}
		.content{
			width: 100% ;
			height: 100% !important;
		}
		.container{
			width: 100% !important;
			height: 370px !important;
			padding: 30px !important;
			margin-top: 10px !important;
			margin-left: 0px !important;
			position: relative;
			
		}
	}
</style>


