<template>
	<div class="page_box">
		<div class="topper">
		  <div class="topper_t"><a href="./index.html">城市智能管理中心</a></div>
		  <div class="logo flex">
		    <div><img class="fl" src="../../static/img/logo.png" alt=""></div>
		    <div>
		      <p class="logo_b">上海联通大数据中心</p>
		      <p class="logo_s">智能时代 忠于创新</p>
		    </div>
		  </div>
		  <div class="topper_nav clear">
		  	<div class="nav_l fl flex">
		  		<router-link  to="/Fire">消防安全</router-link>
		  		<router-link to="/Community">社区安全</router-link>
		  		<router-link to="/Food">食品安全</router-link>
		  		<router-link to="/Street">街道安全</router-link>
		  		<!-- <router-link to="/Water">水质管理</router-link> -->
		  		<a class="disabled">水质管理</a>
		  	</div>
		  	<div class="nav_r fr flex">
		  		<!-- <router-link to="/Flood">窨井管理</router-link> -->
		  		<a class="disabled">窨井管理</a>
		  		<!-- <router-link to="/Park">停车管理</router-link> -->
		  		<a class="disabled">停车管理</a>
		  		<router-link to="/Market">市场监管</router-link>
		  		<router-link to="/Garbage">垃圾管理</router-link>
		  		<router-link to="/Aged">养老服务</router-link>
		  		<!-- <router-link to="/Environ">环保服务</router-link> -->
		  		<a class="disabled">环保服务</a>
		  	</div>
		  </div>
		</div>
		<div class="main flex">
		  <!-- 左边 -->
		  <div class="main_l flex_item">
		  	<slot name="left"></slot>
		  </div>
		  <!-- 中间 -->
		  <div class="main_c flex_item">
		    <slot name="center"></slot>
		  </div>
		  <!-- 右 -->
		  <div class="main_r flex_item">
		    <slot name="right"></slot>
		  </div>
		</div>
	</div>
</template>

<script>
export default {
	name: '',
	data () {
		return {
		  
		}
	},
	mounted(){
		$( this.$el ).find('.disabled').css({
			// 'pointer-events': 'none',
			cursor: 'not-allowed'
		})
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
	@font_big: 42px;
	@wl: 1240px;
	@wc: 1430px;
	@wr: 1170px;
	.page_box{
		width: 100%;
		height: 100%;
		padding-top: 20px;
	}
	.topper{
		height: 80px;
		margin: 0 0 10px 0;
		background: url('../../static/img/top_title_bg.png') no-repeat center;
		position: relative;
		.topper_t{
			margin: 0 auto;
			height: 80px;
			width: 610px;
			a{
				color: #00fff7;
			}
			text-align: center;
			font-size: @font_big;
			line-height: 80px;
			font-weight: bold;
		}
		.logo{
			position: absolute;
			height: 44px;
			width: 400px;
			top: 10px;
			bottom: 0;
			left: 30px;
			vertical-align: middle;
			align-items: center;
			p{
				color: #eaeaea;
				padding-left: 10px;
			}
			.logo_b{
				font-size: 24px;
			}
			.logo_s{
				font-size: 14px;
				letter-spacing: 5px;
				text-align: center;
				white-space: nowrap;
			}
			img{
				width: 53px;
			}
		}
	}
	.topper_nav{
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		text-align: center;
		.flex{
			justify-content: space-between;
		}
		.nav_l{
			width: @wl;
			padding: 0 60px 0 360px;
		}
		.nav_r{
			width: @wr;
			padding: 0 60px;
		}
		a{
			font-size: 20px;
			flex: 0 0 auto;
			padding: 5px 20px;
			background-image: linear-gradient(0deg, 
					rgba(17, 174, 239, 0.37) 0%, 
					rgba(17, 174, 239, 0.14) 100%);
			border: solid 1px #00bffe;
			border-radius: 8px;
			&:hover{
				color: #00fff0;
			}
			&.router-link-active{
				color: #00fff0;
			}
		}
	}
	.main{
		height: 970px;
		overflow: hidden;
	}
	.main_l{
		flex: 0 0 @wl;
		overflow: hidden;
		height: 100%;
	}
	.main_c{
		flex: 0 0 @wc;
		overflow: hidden;
		height: 100%;
	}
	.main_r{
		flex: 0 0 @wr;
		overflow: hidden;
		height: 100%;
	}
	.center_1{
		.cont{
			width: 100%;
			height: 100%;
		}
	}


</style>


