


// 上线环境不执行
export function arrayCopy( arr ){
	var _arr = [];
	arr.forEach(function (item, index) {
		_arr.push( item );
	})
	return _arr;
}

export function getPerc(arr){
  var _arr = arrayCopy(arr);
  _arr.sort(function (val1, val2) {
  	return val2 - val1;
  });
  // console.log( _arr[0] );
  return arr.map(function(elem, index) {
  	return Math.round(elem/_arr[0]*100);
  })
}

export function formatChartList( list ){
  var _list = arrayCopy( list );
  _list.sort( function(item1, item2) {
    return item2.num_alert - item1.num_alert
  });
  var xList = [];
  var data1 = [];
  var data2 = [];
  _list.forEach( function(item, index) {
    xList.push( item.district_name );
    data1.push( item.num_alert );
    data2.push( _list[0].num_alert );
  });
  return {
    xList,
    data1,
    data2
  }
}

export function insertScript(src){
  var script = $( '<script data-type=static src="'+src+'"><\/script>' );
  script[0].onreadystatechange = function () {
    alert(123)
  }
  script[0].onload = function () {
    console.log(111111)
  }
  script[0].onerror = function () {
    console.log(2222)
  }
  $('#myScript').html( script );
}

/**
 * 获取对象的多层键值，获取不到返回''
 * @param  {[obj]} obj [获取值的对象]
 * @param  {[type]} str [获取的路径字符串]
 * @return {[type]}     [description]
 */
function getVal( obj, str, noStr ){
  if ( !obj ) {
    return noStr || '--';
  }
  var arr = str.replace('[', '.').replace(']', '').split('.');
  var res = noStr || '--';
  _isObj(obj, arr );
  return res;
  // 循环需要遍历的键
  function _isObj(obj, arr ){
    
    if ( !obj[arr[0] ] ) {
      return '';
    }else if( arr.length === 1 ){
      res = obj[arr[0]] ;
      // console.log( res )
    }else{
      var first = arr.shift();
      _isObj(obj[first], arr );
    }
  }
}

function isArrayFn(value){
  if (typeof Array.isArray === "function") {
    return Array.isArray(value);
  } else {
    return Object.prototype.toString.call(value) === "[object Array]";
  }
}

function myPromise(jsonUrl, dataUrl){
  return new Promise(function (reosolve, reject) {
    let ajaxIndex = 0;
    let json ;
    let data ;

    $.ajax({
      url: jsonUrl,
      type: 'get',
      success(response){
        if ( typeof response != 'object') {
          json = JSON.parse(response);
        }else{
          json = response;
        }
        
        ajaxIndex++;
        handleData();
      },
      error(err){
        reject(err)
      }
    });

    $.ajax({
      url: dataUrl,
      type: 'get',
      success(response){
        if ( typeof response != 'object') {
          data = JSON.parse(response);
        }else{
          data = response;
        }
        ajaxIndex++;
        handleData();
      },
      error(err){
        reject(err)
      }
    });

    function handleData(){
      if ( ajaxIndex === 2 ) {
        reosolve({json, data});
      }
    };
  });
}

export const Common = {
  getVal,
  myPromise
}



