<!-- 规划部署 -->
<template>
  <div class="content">

    <div class="title">
      <img class="imgtit" src="../../static/img/deyplaylogo.png"/>
      <span class="title1">{{ static.title1 }}</span>
      <span class="title2">{{ static.title2 }}</span>
    </div>
    <div class="container" style="">
      <img  class="img1" src="../../static/img/bushu.png"/>
      <span class="getvalue1">{{ static.num1 }}</span>
      <div><span class="chuangan">{{static.desc1}}</span></div>

      <img class="img2"  src="../../static/img/guihua.png"/>
      <span class="getvalue2">{{ static.num2 }}</span>
      <div><span class="laoren">{{static.desc2}}</span></div>
    </div>
  </div>
</template>

<script>
export default {
	name: '',
	props: {
		num1:{
			default: '--'
		},
		num2:{
			default: '--'
		},
    static: {
      default(){
        return {
          title1: "已部署",
          num1: 123,
          desc1: "(个)",
          title2: "规划部署",
          num2: 23,
          desc2: "(个)"
        }
      }
    }
	},
	data () {
		return {

		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .content{
    margin:0;
    height: 265px;
    width:798px;
  }

  .title{
    font-size:30px;
    color:#00ffee;
    margin-top:17px;
  }
  .imgtit{
    float:left;
    margin-left:50px;
  }
  .title1{
    margin-left:95px;
    margin-right:210px;
  }

  .container{
    height: 205px;
    margin-left:160px;
  }
  .chuangan{
    float:left;
    color:#ffffff;
    margin-left:-55px;
    margin-top:170px;
    font-size:24px;
  }
  .img1{
    margin-top:14px;
    margin-left:0px;
    position:absolute;
    z-index:0;

  }
  .getvalue1{
    color:#ffffff;
    font-size:24px;
    float:left;
    margin-left:50px;
    margin-top:80px;
    width: 60px;
    text-align: center;
  }

  .laoren{
    float:left;
    color:#ffffff;
    margin-left:276px;
    margin-top:170px;
    font-size:24px;
  }

  .img2{
    margin-top:14px;
    margin-left:200px;
    position:absolute;

  }
  .getvalue2{
    color:#ffffff;
    font-size:24px;
    float:left;
    margin-left:269px;
    margin-top:85px;
    position:absolute;
    z-index:2;
    width: 60px;
    text-align: center;
  }

</style>



