export function initLineTwo01(dom, myData, outOpt) {
	// debugger
	outOpt = outOpt || {}
	var myChart;
	if ($(dom).find('canvas').length > 0) {
		myChart = echarts.getInstanceByDom(dom)
	} else {
		myChart = echarts.init(dom);
	}

	var option = {
		grid: {
			bottom: '5%',
			top: '10%',
			left: '10%',
			right: '10%',
		},
		tooltip: {
			trigger: 'axis'
		},
		color: ['#70bae9', '#f1a384'],
		legend: {
			icon: 'rect',
			right: '10%',
			top: '0%',
			itemWidth: 20,
			itemHeight: 10,
			itemGap: 10,
			textStyle: {
				fontSize: 14,
				color: '#fff'
			}
		},
		xAxis: {
			type: 'category',
			data: myData.dataList.map(function(item) {
				return item.name
			}),
			axisTick: {
				show: false
			},
			axisLabel: {
				textStyle: {
					fontSize: 14,
					color: '#cccccc',
				}
			},
			axisLine: {
				show: false
			},
			splitLine: {
				show: true,
				lineStyle: {
					color: '#b4b8bc',
				}
			},
		},
		yAxis: {
			type: 'value',
			axisTick: {
				show: false
			},
			splitLine: {
				show: true,
				lineStyle: {
					color: '#b4b8bc',
				}
			},
			axisLabel: {
				show: false
			},
			axisLine: {
				show: false
			},
			splitArea: {
				height: '50%',
				show: true,
				areaStyle: {
					color: ['#1c3e58', '#1c3e58'],
					shadowColor: '#1c3e58',
					shadowBlur: 10
				}
			},
		},
		series: [{
				name: myData.name1,
				data: myData.dataList.map(function(item) {
					return item.value1
				}),
				type: 'line',
				smooth: true,
				//symbol: 'circle',
				itemStyle: {
					normal: {

						lineStyle: {
							color: "#70bae9"
						}
					}
				},
			}, {
				name: myData.name2,
				data: myData.dataList.map(function(item) {
					return item.value2
				}),
				type: 'line',
				smooth: true,
				//symbol: 'circle',
				itemStyle: {
					normal: {

						lineStyle: {
							color: "#f1a384"
						}
					}
				},
			}

		]
	};

	if (option && typeof option === "object") {
		myChart.setOption(option, true);
		myChart.setOption(outOpt, false);
	}
}


