<template>
<div class="warn_sum2">
	<div class="com_title">
		<img class="icon" src="../../static/img/icon_people3.png" alt="">{{static.title}}
	</div>
	<div class="cont">
		<table class="table">
			<tbody>
				<tr>
					<td class="col_1 pic_box" rowspan="2"><img src="../../static/img/ling.png"></td>
					<td class="col_2">{{static.desc1}} </td>
					<td class="col_3 num">{{static.num1}}<span class="sub">{{static.unit}}</span></td>
				</tr>
				<tr>
					<td class="col_2">{{static.desc2}}</td>
					<td class="col_3 num">{{static.num2}}<span class="sub">{{static.unit}}</span></td>
				</tr>
			</tbody>
		</table>
	</div>
</div>
</template>

<script>
export default {
	name: '',
	props: {
		static: {

		}
	},
	data () {
		return {
		  
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
	.com_title{
		font-size: 30px;
		line-height: 60px;
		letter-spacing: 0px;
		color: #00fff0;
		padding-left: 20px;
		.icon{
			width: 40px;
			height: 40px;
			padding: 10px;
			vertical-align: middle;
		}
	}
	.cont{
		padding-top: 60px;
	}
	.table{
		width: 100%;
	}
	.pic_box{
		padding: 0 10px;
		vertical-align: middle;
	}
	.col_1{

	}
	.col_2{
		text-align: left;
	}
	.col_3{

	}
	.num{
		font-size: 40px;
		color: #f84242;
	}
	.sub{
		padding-left: 10px;
		font-size: 20px;
	}
	
	
</style>


