export function initLineBar01(dom, myData, outOpt) {
	// debugger
	outOpt = outOpt || {}
	var myChart;
	if ($(dom).find('canvas').length > 0) {
		myChart = echarts.getInstanceByDom(dom)
	} else {
		myChart = echarts.init(dom);
	}
	var option = {
		grid: {
			bottom: '15%',
			top: '25%',
			left: '0%',
			right: '5%',
		},
		tooltip: {
			trigger: 'axis'
		},
		color: ['#ffab89', '#7ecefd'],
		legend: {
			icon: 'rect',
			right: '5%',
			top: '3%',
			itemWidth: 38,
			itemHeight: 18,
			itemGap: 40,
			textStyle: {
				fontSize: 20,
				color: '#fff'
			}
		},
		xAxis: {
			type: 'category',
			name: "时间",
			nameTextStyle: {
				color: "#fff",
				align: "left",
				fontSize: 16,
				padding: [0, 0, 0, -36]
			},
			data: myData.dataList.map(function(item) {
				return item.name
			}),
			axisTick: {
				show: false
			},
			axisLabel: {
				textStyle: {
					fontSize: 14,
					color: '#cccccc',
				}
			},
			axisLine: {
				show: false
			},
			splitLine: {
				show: false,
			}
		},
		yAxis: {
			type: 'value',
			axisTick: {
				show: false
			},
			splitLine: {
				show: false,
			},
			axisLine: {
				show: false
			},
			axisLabel: {
				show: false
			},
		},
		series: [{
				name: myData.name1 || '',
				data: myData.dataList.map(function(item) {
					return item.value1
				}) || [],
				type: 'line',
				itemStyle: {
					normal: {
						areaStyle: {
							color: '#20425d',
						},
						lineStyle: {
							color: '#ffab89'
						}
					}
				},
			}, {
				name: myData.name2|| '',
				data: myData.dataList.map(function(item) {
					return item.value2
				}) || [],
				type: 'bar',
				barWidth: 18,
				itemStyle: {
					normal: {
						lineStyle: {
							color: '#7ecefd'
						}
					},
				},
			}
		]
	};

	if (option && typeof option === "object") {
		myChart.setOption(option, true);
		myChart.setOption(outOpt, false);
	}
}


